.why {
  padding: 85px 0;

  &__wrapper {
    display: flex;
  }

  &__left {
    display: flex;
    flex-direction: column;
    width: 270px;
    min-width: 270px;
    margin-right: 132px;
  }

  &__header {
    font-weight: bold;
    font-size: 30px;
    line-height: 35px;
    color: $black;
    margin-bottom: 25px;
  }

  &__subheader {
    font-size: 15px;
    line-height: 25px;
    color: $black;
  }

  &__right {
    display: flex;
    justify-content: space-between;
    width: 100%;

    &-col {
      display: flex;
      flex-direction: column;
      width: 170px;
    }

    &-item {
      display: flex;
      justify-content: space-between;
      margin-bottom: 75px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-icon {
      width: 32px;
      height: 32px;
      margin-right: 25px;
      margin-top: 10px;
    }

    &-text {
      font-weight: 500;
      font-size: 15px;
      line-height: 25px;
      color: $black;
    }
  }
}
