.footer {
  padding: 41px 0;

  &__list {
    display: flex;
  }

  &__item {
    width: 25%;
  }
}

.footer-item {
  display: flex;
  flex-direction: column;

  &__copyright {
    font-size: 15px;
    line-height: 25px;
    color: $black;
  }

  &__phone,
  &__mail {
    font-weight: bold;
    font-size: 15px;
    line-height: 25px;
    color: $black;
  }

  &__phone {
    margin-bottom: 10px;
  }

  &__socials {
    display: flex;
  }

  &__social {
    width: 25px;
    height: 25px;
    margin-right: 5px;
  }

  &__name,
  &__address {
    font-size: 15px;
    line-height: 25px;
    color: $black;
  }

  &__name {
    opacity: 0.6;
    margin-bottom: 23px;
  }

  &__address {
    font-weight: bold;
  }
}
