.header-filters {
  position: relative;
  top: 0;
  padding: 20px 0;
  background-color: $blue;

  &_active {
    background-color: #fff;

    & .header-filters__wrapper {
      justify-content: flex-start;
    }

    & .header-filters__close {
      visibility: visible;
      opacity: 1;
      display: flex;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__search-form {
    width: 100%;
    max-width: 270px;

    &_active {
      border-radius: 0;
      border-bottom: 1px solid #015FA8;
    }
  }

  &__search {
    width: 100%;
  }

  &__close {
    display: none;
    visibility: hidden;
    align-items: center;
    cursor: pointer;
    z-index: 100;
    position: relative;
    right: -20px;
    margin-right: -70px;
    font-size: 13px;
    line-height: 20px;
    color: #909499;
    opacity: 0;
    will-change: opacity;
    transition: opacity 0.3s;

    .icon {
      min-width: 14px;
      width: 14px;
      height: 14px;
      margin-right: 8px;
    }
  }

  &__text {
    display: inline-block;
    margin: 0 33px 0 30px;
    font-size: 15px;
    line-height: 25px;
    color: $white;
  }

  &__group {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: unset;
    width: 100%;

    &-select {
      width: 215px;
    }

    &-btn {
      width: 132px;
    }
  }

  &__popup {
    max-width: 420px;
    z-index: 99;
    visibility: hidden;
    opacity: 0;
    transition: .3s;
    cursor: default;
    position: absolute;
    width: 420px;
    background: #fff;
    top: 128%;
    border: 1px solid #E1E3E6;
    border-radius: 5px;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
    right: auto;
    left: 14%;
    display: flex;
    flex-direction: column;
    padding: 17px 20px 24px;
    text-align: center;
    color: $black;

    transform: translateX(-50%);

    &::before {
      content: '';
      position: absolute;
      right: auto;
      left: 50%;
      top: -6px;
      width: 12px;
      height: 12px;
      background-color: $white;
      transform: translateX(-50%) rotate(45deg);
      border: 1px solid #E1E3E6; 
    }

    &::after {
      content: '';
      position: absolute;
      right: auto;
      left: 50%;
      top: 0px;
      width: 20px;
      height: 12px;
      background-color: $white;
      transform: translateX(-50%);
    }

    p {
      margin: 0 0 16px;
      font-size: 12px;
      line-height: 18px;
    }

    .icon-circle {
      width: 68px;
      height: 68px;
      margin-bottom: 16px;
      position: relative;

      .icon {
        width: 35px;
        height: 27px;
        margin: 0;
      }
    }

    &.active {
      visibility: visible;
      opacity: 1;
    }

    &-title {
      font-size: 16px;
      font-weight: 500;
      margin-bottom: 16px;
    }

    .link {
      cursor: pointer;
      text-decoration: underline;
      font-size: 12px;
      font-weight: bold;
      color: #026CBF;
      
      &:not(:last-of-type) {
        margin-bottom: 16px;
      }
    }
  }
}

.header-overlay {
  display: none;
  background-color: #fff;
  position: absolute;
  top: 100%;
  left: 0;
  height: 680px;
  width: 100%;
  z-index: -1;
  opacity: 0;

  .overlay {
    position: fixed;
    top: 680px;
    right: 0;
    left: 0;
    bottom: 0;
    backdrop-filter: blur(2px);
    background-color: #000;
    z-index: 150;
    height: 100vh;
    visibility: hidden;
  }

  &_active {
    display: block;
    z-index: 98;
    opacity: 1;

    .overlay {
      visibility: visible;
      opacity: 0.9;
    }

    & .header-overlay__result {
      opacity: 1;
    }
  }
  
  &__result {
    opacity: 0;
    position: absolute;
    top: 20px;
    transform: translateX(-50%);
    left: 50%;
    height: 60%;
    width: 100%;
    max-width: 1200px;
    padding: 0 15px;

    &-wrap {
      overflow: auto;
      height: 100%;
      max-width: 42.5%;
      width: 100%;
    }

    &-title {
      font-size: 16px;
      font-weight: bold;
      color: #141414;
      margin-bottom: 8px;
    }

    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      padding-bottom: 20px;

      &:last-of-type {
        padding-bottom: 70px;
      }

      li {
        line-height: 12px;

        &:not(:last-of-type) {
          margin-bottom: 8px;
        }

        a {
          font-size: 14px;
          line-height: 20px;
          color: #141414;
          text-decoration: none;
          display: inline-block;

          &:hover {
            color: #026CBF;
          }
        }
      }
    }
  }

  &__contact {
    width: 100%;
    max-width: 520px;
    position: absolute;
    width: 100%;
    max-width: 520px;
    top: 0;
    right: 15px;
    font-size: 14px;
    line-height: 20px;
    color: #141414;

    &-wrap {
      display: flex;
      align-items: center;
      margin-top: 15px;
      font-size: 15px;
      line-height: 18px;

      .icon {
        width: 37px;
        height: 42px;
        margin-right: 16px;
      }
    }

    &-tel {
      font-size: 24px;
      font-weight: bold;
      color: #141414;
      text-decoration: none;
      display: inline-block;

      &:hover {
        color: #026CBF;
      }

      // &:first-of-type {
      //   border-right: 1px solid #C4C8CC;
      //   padding-right: 16px;
      //   margin-right: 16px;
      // }
    }
  }
}