.select {
  padding: 10px 46px 10px 15px;
  font-size: 15px;
  line-height: 25px;
  background-color: $white;
  border-radius: 5px;
  background: $white url('../img/redesign/design/arrow-down.svg') no-repeat;
  background-position: top 50% right 15px;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
  border: none;

  &::-ms-expand {
    display: none;
  }

  &:invalid {
    color: rgb(117, 117, 117);
  }

  & option[value=''][disabled] {
    display: none;
  }

  & option {
    color: $black;
  }

  &.disabled {
    pointer-events: none;
    background-color: #F0F3F8;
    color: #909499;
  }
}
