/* Demo Page styles */
html,
body {
  -webkit-overflow-scrolling: touch;

  // min-width: $max-content-width;
  // height: 100%;
  // min-height: 100%;
}

html {
  &.lock {
    overflow: hidden;
    height: 100vh;
  }
}

body {
  position: relative;
  font-family: Roboto, 'Helvetica Neue', sans-serif;

  &.lock {
    overflow: hidden;
    height: 100vh;
  }
}

main {
  padding-top: 165px;
}

.content-wrapper {
  max-width: $max-content-width;
  margin: 0 auto;
}

.icon-circle {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 24px;
  background-color: #F0F3F8;
  border-radius: 100%;
  width: 100px;
  height: 100px;
  .icon {
    height: 55px;
  }
}
