.spares {
  padding: 92px 0 103px;

  background-image: url('../img/redesign/design/spares/bg.png');
  background-position: top 0 right 0;
  background-size: 880px, 500px;
  background-repeat: no-repeat;

  &__wrapper {
    position: relative;
    display: flex;
  }

  &__col {
    display: flex;
    flex-direction: column;

    &_right {
      position: absolute;
      top: 22px;
      left: 466px;
    }
  }

  &__header {
    max-width: 500px;
    font-weight: bold;
    font-size: 45px;
    line-height: 55px;
    color: $black;
    margin-bottom: 30px;
  }

  &__subheader {
    max-width: 205px;
    margin-bottom: 40px;
    font-size: 15px;
    line-height: 25px;
    color: $black;
  }

  &__button {
    padding: 13px 30px 12px;
    width: 250px;
    border-radius: 10px;
  }

  &__icon {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-right: 10px;

    &_last {
      margin-right: 15px;
    }
  }
}
