.call-btn {
  z-index: 1;
  position: fixed;
  display: flex;
  text-align: left;
  align-items: center;
  padding: 20px;
  font-weight: 500;
  font-size: 13px;
  line-height: 15px;
  color: $white;
  transform: rotate(-90deg);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background-color: $blue;
  top: 404px;
  top: 460px;
  right: -57px;
  width: 184px;

  &:hover {
    cursor: pointer;
  }

  & p {
    display: flex;
    zoom: 1.005;

    & > img {
      margin-right: 15px;
    }
  }
}