.modal {
  $self: &;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 11111;
  display: none;

  .title {
    font-size: 23px;
    font-weight: 400;
    margin-bottom: 20px;
    line-height: 34px;
    width: 100%;
  
    &_modal {
      font-size: 20px;
      font-weight: 500;
      text-align: center;
    }
  }

  .text {
    margin-bottom: 4px;
    font-size: 23px;
    font-weight: 400;
    line-height: 34px;
  }
  
  .subtitle {
    font-size: 14px;
    margin-bottom: 20px;

    &_modal {
      text-align: center;
    }
  }

  & .form {
    $self: &;
  
    .error {
      &__text {
        color: #ED2C2C;
        line-height: 20px;
        margin-top: 4px;
      }
    }
  
    &__group {
      &:not(:last-of-type) {
        margin-bottom: 20px;
      }
  
      &.error {
        #{$self}__input {
          border-color: #ED2C2C;
          background-color: #FFF2F2;
        }
      }
  
      &.disabled {
        #{$self}__label {
          color: #909499;
        }
  
        #{$self}__input {
          pointer-events: none;
          color: #909499;
          background-color: #F0F3F8;
  
          &::placeholder {
            color: #C4C8CC;
          }
        }
      }
    }
    
    &__label {
      margin-bottom: 4px;
      font-size: 12px;
      display: block;
      text-align: left;
    }
  
    &__input {
      border: 1px solid #E1E3E6;
      border-radius: 5px;
      padding: 15px 14px;
      width: 100%;
  
      &:focus {
        border-color: #C4C8CC;
      }
  
      &::placeholder {
        color: #909499
      }
    }
  
    &__textarea {
      border: 1px solid #E1E3E6;
      border-radius: 5px;
      padding: 16px 14px;
      resize: none;
      width: 100%;
      min-height: 100px;
      &:focus {
        outline: none;
      }
    }
  
    &__info {
      display: block;
      margin-top: 4px;
      color: #909499;
    }
  
    &__checkbox {
      user-select: none;
      display: flex;
      align-items: center;
      position: relative;
      min-height: 20px;
      text-align: left;
  
      label {
        padding-left: 28px;
        z-index: 1;
        cursor: pointer;
      }

      span {
        font-size: 12px;
      }
  
      a {
        text-decoration: underline;
        display: inline;
        color: #141414;

        &:hover {
          color: #026CBF;
        }
      }
  
      input {
        display: none;
  
        + .form__checkbox-mark {
          position: absolute;
          top: 0;
          left: 0;
          content: '';
          width: 20px;
          height: 20px;
          border: 1px solid #E1E3E6;
          margin-right: 8px;
          border-radius: 5px;
        }
  
        &:checked + .form__checkbox-mark {
          border-color: #015FA8;
          background-color: #015FA8;
          
          &::before {
            position: absolute;
            content: '';
            width: 5px;
            top: 9px;
            left: 4px;
            height: 2px;
            background-color: #fff;
            transform: rotate(45deg);
          }
  
          &::after {
            width: 10px;
            height: 2px;
            top: 8px;
            left: 6px;
            transform: rotate(-45deg);
            position: absolute;
            content: '';
            background-color: #fff;
          }
        }
      }
    }
  
    &__button {
      width: 100%;
      margin-top: 20px;
      font-size: 13px;
      height: 56px;
    }
  
    &__back {
      display: block;
      text-align: center;
      font-weight: bold;
      margin-top: 20px;
      text-decoration: underline;
      font-size: 14px;
      line-height: 16px;
      color: #015FA8;
    }
  
    &__order {
      margin-bottom: 24px;
      &-title {
        font-size: 17px;
        font-weight: 600;
        margin-bottom: 12px;
        // @include breakpoint(xl) {
        //   @include fontSize(16px);
        // }
      }
      &__item {
        padding: 5px 17px;
        border-left: 5px solid #F0F3F8;
        font-size: 14px;
        &:not(:last-of-type) {
          margin-bottom: 16px;
        }
        &-name {
          font-weight: 600;
        }
        &-model, &-code {
          color: #909499;
          margin-top: 4px;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }
        &-price {
          margin-top: 4px;
        }
      }
    }
  }

  &__wrap {
    position: relative;
    max-width: 1100px;
    width: 100%;
    height: 100%;
    max-height: 80vh;
    z-index: 11000;
    margin: 20px 130px;
    display: none;

    // @include breakpoint(xl) {
    //   margin: 20px 10px;
    //   height: auto;
    // }
  }

  &__content {
    padding: 60px 30px;
    overflow: auto;
    height: auto;
    width: 100%;
    border: 1px solid #E1E3E6;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    border-radius: 5px;
    text-align: center;

    // @include breakpoint(xl) {
    //   padding: 24px 15px;
    // }
  }

  &__overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 10000;
    backdrop-filter: blur(2px);
    background-color: #141414;
    opacity: 0.3;
    display: none;
  }

  &__close {
    display: flex;
    font-size: 12px;
    align-items: center;
    cursor: pointer;
    position: absolute;
    top: -60px;
    right: 0;
    width: 120px;
    height: 46px;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    padding-left: 50px;
    color: $white;

    &:before {
      top: 50%;
      left: 15px;
      transform: translateY(-50%) rotate(45deg);
      content: '';
      position: absolute;
      width: 14px;
      height: 2px;
      background-color: $white;
    }

    &:after {
      top: 50%;
      left: 21px;
      transform: translateY(-50%) rotate(45deg);
      content: '';
      position: absolute;
      width: 2px;
      height: 14px;
      background-color: $white;
    }
  }

  &_main {
    & .modal__wrap {
      max-width: 530px;
      height: auto;
    }
  }
  
  [data-modal=contacts] {
    .header__buttons::before {
      display: none;
    }
  }
}