/* Colour palette */
$black: #000;
$white: #fff;
$light-gray: #f1fafe;
$yellow: #ffdb5b;
$blue: #427cba;
$red: #d02624;

$max-content-width: 1170px;

$breakpoints: (
  sm: 640px,
  md: 768px,
  lg: 631px,
  xl: 1199px,
  xxl: 1440px
);

@mixin breakpoint($value, $keyword: null) {
  @media screen and (max-device-width: map-get($breakpoints, $value)) {
    @content
  }
}