.contacts {
  position: relative;
  padding: 82px 0 108px;

  &__bg {
    background-image: url('../img/redesign/design/iphone.png');
    background-position: top 0 right 50%;
    background-size: 634px, 500px;
    background-repeat: no-repeat;
    width: 634px;
    height: 500px;
    top: 0px;
    left: calc(50% - 361px);
    position: absolute;
    pointer-events: none;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 60px 50px;
    background-color: $blue;
    border-radius: 10px;
  }

  &__col {
    display: flex;
    flex-direction: column;

    &_left {
      width: 270px;
      padding-top: 11px;
    }
  }

  &__header {
    margin-bottom: 25px;
    font-weight: bold;
    font-size: 30px;
    line-height: 35px;
    color: $white;
  }

  &__subheader {
    font-size: 15px;
    line-height: 25px;
    color: $white;
  }

  &__form {
    display: flex;
    flex-direction: column;
  }

  &__message,
  &__phone {
    width: 370px;
  }

  &__message {
    height: 90px;
    margin-bottom: 20px;
    resize: none;
  }

  &__phone {
    margin-bottom: 20px;
  }

  &__btn {
    width: 191px;
  }
}
