@import '../../../../node_modules/flickity/dist/flickity';

.guarantee {
  padding: 54px 0 35px;

  & .flickity-viewport {
    height: 400px !important;
  }
}

.guarantee-item {
  display: flex;
  border-radius: 10px;
  background-color: $light-gray;

  &.carousel-cell {
    width: 100%;
    height: 400px;
  }

  &__img {
    object-fit: contain;
    max-width: 670px;
    width:100%;
    flex-shrink: 0;
  }

  &__content {
    padding: 55px 58px 55px 60px;
    width: 100%;
  }

  &__title {
    font-weight: bold;
    font-size: 30px;
    line-height: 35px;
    color: $black;
    margin-bottom: 25px;
  }

  &__text {
    font-size: 15px;
    line-height: 25px;
    color: $black;
    margin-bottom: 60px;
  }

  &__buttons {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }

  &__btn {
    width: 173px;
    border-radius: 10px;
  }

  &__arrows {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 64px;
  }

  &__arrow {
    width: 24px;
    height: 24px;

    &:hover {
      cursor: pointer;
      opacity: 0.6;
    }

    &_left {
      transform: rotate(180deg);
    }

    &_disabled {
      opacity: 0.4;
      pointer-events: none;
    }
  }
}
