.button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 40px;
  color: $white;
  font-weight: 500;
  font-size: 13px;
  line-height: 15px;
  border-radius: 5px;

  &:hover {
    cursor: pointer;
    color: $white;
  }

  &_red {
    background-color: $red;
  }

  &_blue {
    background-color: $blue;
  }
}
