.icon {
  width: 100%;
  height: 100%;

  &_black {
    fill: #141414;
  }

  &_white {
    fill: #fff;
  }

  &_primary {
    fill: #0E7BCF;
  }

  &_secondary {
    fill: #015FA8;
  }

  &_grey {
    fill: #C4C8CC;
  }

  &_darkgrey {
    fill: #909499;
  }
  
  &_lightgrey {
    fill: #E1E3E6;
  }

  &_red {
    fill: #D6042A;
  }
}