.input {
  padding: 10px 46px 10px 15px;
  font-size: 15px;
  line-height: 25px;
  color: $black;
  background-color: $white;
  border-radius: 5px;

  @include placeholder {
    font-size: 15px;
    line-height: 25px;
    color: $black;
    opacity: 0.4;
  }

  &_search {
    background: $white url('../img/redesign/design/search.svg') no-repeat;
    background-position: top 50% right 15px;
  }
}
