.header {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  z-index: 20;

  &_sticky {
    position: fixed;
    width: 100%;
  }

  &__cart-popup {
    z-index: 99;
    visibility: hidden;
    opacity: 0;
    transition: .3s;
    cursor: default;
    position: absolute;
    width: 420px;
    background: #fff;
    border: 1px solid #E1E3E6;
    border-radius: 5px;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
    top: 60px;
    right: -180px;
    display: flex;
    flex-direction: column;
    padding: 17px 20px 24px;
    text-align: center;
    color: $black;
    transform: translateX(-50%);

    &::before {
      content: '';
      position: absolute;
      top: -6px;
      right: 60px;
      width: 12px;
      height: 12px;
      background-color: $white;
      transform: translateX(-50%) rotate(45deg);
      border: 1px solid #E1E3E6;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0px;
      right: 57px;
      width: 20px;
      height: 12px;
      transform: translateX(-50%);
      background-color: $white;
    }

    p {
      margin: 0 0 16px;
      font-size: 12px;
      line-height: 18px;
    }

    .icon-circle {
      width: 68px;
      height: 68px;
      margin-bottom: 16px;
      position: relative;

      .icon-circle__count {
        position: absolute;
        top: 0;
        right: 0;
        width: 17px;
        height: 17px;
        color: $white;
        font-size: 10px;
        font-weight: bold;
        border-radius: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: var(--button-primary);
      }

      .icon {
        width: 30px;
        height: 32px;
        margin: 0;
      }
    }

    &.active {
      visibility: visible;
      opacity: 1;
    }

    .link {
      cursor: pointer;
      text-decoration: underline;
      font-size: 12px;
      font-weight: bold;
      color: #026CBF;
      
      &:not(:last-of-type) {
        margin-bottom: 16px;
      }
    }
  }
}

.header-nav {
  &__wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    &-img {
      width: 280px;
      height: 80px;
    }
  }

  &__links {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  &__phone {
    font-size: 20px;
    line-height: 30px;
    font-weight: bold;
    color: $black;

    &_second {
      margin-left: 30px;
    }
  }

  &__social {
    width: 35px;
    height: 35px;

    margin-left: 5px;

    &_tlg {
      margin-left: 30px;
    }
  }

  &__basket {
    width: 116px;
    margin-left: 40px;
    color: $black;
  }
}

.header-basket {
  position: relative;

  display: flex;
  align-items: center;

  &__count {
    position: absolute;
    top: -12px;
    left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $yellow;
    width: 25px;
    height: 25px;
    border-radius: 25px;
  }

  &__text {
    margin-left: 16px;
    font-size: 13px;
    line-height: 15px;
    font-weight: 500;
    color: $black;
  }
}

